.Loading {
    -webkit-animation: rotate infinite 2s ease;
            animation: rotate infinite 2s ease;
    height: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 50px;
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
  }
}


